'client'
import React, { useEffect, useState } from 'react'
import { getToDo } from '@/services/ToDoServices'

function ToDoList() {
  const [todo, setTodo] = useState([])

  useEffect(()=>{
    const todo = getToDo()
    setTodo(todo)
  }, [])
  return (
    <div className='border m-6 h-auto w-auto'>
      {todo.map((todo)=>{
        <div>
          {todo.title}
        </div>
      })}
    </div>
  )
}

export default ToDoList