import axios from "axios";
import 'dotenv/config'

export const getToDo = async() =>{
    try{
        const url = process.env.TODO_API
        if(url){
            const todo = await axios.get(url, {
                method: 'GET'
            })

            return todo
        }else{
            throw new Error('no api url provided')
        }
    }catch(e){
        console.error(e)
    }
}